:root {
  --main-color: #5ea3d0;
  --secondary-color: white;
  --main-text-color: #3e5869;
  --secondary-text-color: #b0c7d6;
  --send-message-form: #F5F5F5;
}

html, body {
  height: 100%;
  margin: 0;
  padding: 0;
  font-family: system-ui;
  font-weight: 200;

}

#root {
  height: 100%;
}

.app {
  display: grid;
  height: 100%;
  grid-template-columns: repeat(6, 1fr);
  grid-template-rows: 1fr 1fr 1fr 1fr 1fr 60px;
  grid-template-areas: 
      "r m m m m m"
      "r m m m m m"
      "r m m m m m"
      "r m m m m m"
      "r m m m m m"
      "n f f f f f";
}

.new-room-form {
  grid-area: n;
}

.rooms-list {
  grid-area: r;
}

.message-list {
  grid-area: m;
}

.send-message-form {
  grid-area: f;
}

/* REST OF CSS */

.new-room-form {
  padding: 0 5px;
  background: white;
  background: var(--secondary-color);
  color: #3e5869;
  color: var(--main-text-color);
  overflow: hidden;
  background-color: #2d2e30;
}

.new-room-form form {
  height: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.new-room-form input {
  width: 100%;
  font-size: 18px;
  padding: 10px 60px 10px 15px;
  background-color: #2d2e30;
  color: #9498a1;

}

.new-room-form button {
  background: white;
  background: var(--secondary-color);
  color: #9498a1;
  border: 0;
}
.new-room-form input::-webkit-input-placeholder {
  color: #9498a1;
  font-weight: 200;
}
.new-room-form input::-ms-input-placeholder {
  color: #9498a1;
  font-weight: 200;
}
.new-room-form input::placeholder {
  color: #9498a1;
  font-weight: 200;
}

.new-room-form input:focus {
  outline-width: 0;
}

.new-room-form input {
  border: 0;
}

.new-room-form button {
  border: 0;
}

.message {
  margin: 50px 0;
  display: block;
  position: relative;
  float: right;
  clear: right;

}

.message .message-username {
  font-size: 11px;
  color: #3e5869;
  color: var(--main-text-color);
  opacity: 0.9;
  margin-bottom: 0;
  margin-right: 20px;
  float: right;
  clear: right;
}
.message .message-text {
  background: #5ea3d0;
  background: var(--main-color);
  color: white;
  color: var(--secondary-color);
  display: inline;
  padding: 4px 8px;
  border-radius: 8px;
  float: right;
  clear: right;
}

.message-list {
  box-sizing: border-box;
  padding-left: 6px;
  width: 100%;
  height: 100%;
  overflow: scroll;
  background: white;
  background: var(--secondary-color);
}

.message-list .join-room {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  font-size: 34px;
  font-weight: 300;
}

.send-message-form {
  background: #F5F5F5;
  background: var(--send-message-form);
  display: flex;
}

.send-message-form input {
  width: 100%;
  padding: 15px 10px;
  margin: 0;
  border-style: none;
  background: #F5F5F5;
  background: var(--send-message-form);
  font-weight: 200;

}

.send-message-form input:focus {
  outline-width: 0;
}

.send-message-form input::-webkit-input-placeholder {
  color: #3e5869;
  color: var(--main-text-color);
}

.send-message-form input::-ms-input-placeholder {
  color: #3e5869;
  color: var(--main-text-color);
}

.send-message-form input::placeholder {
  color: #3e5869;
  color: var(--main-text-color);
}


.help-text {
  position: absolute;
  top: 10px;
}

.insert-user {
  background: #F5F5F5;
  background: var(--send-message-form);
  display: flex;

}

.insert-user input {
  width: 100%;
  padding: 15px 10px;
  font-size: 18px;
  margin: 0;
  border-style: none;
  background: #F5F5F5;
  background: var(--send-message-form);
  font-weight: 200;

}

.insert-user input:focus {
  outline-width: 0;
}

.insert-user input:placeholder {
  color: #3e5869;
  color: var(--main-text-color);
}

::-webkit-scrollbar {
  width: 0px;
  background: transparent; /* make scrollbar transparent */
}
.App {
  text-align: center;
  display: grid;
  height: 100%;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
  grid-template-rows: 1fr 1fr 1fr 1fr 1fr 30px 60px;
  grid-template-areas: 
      "r m m m m"
      "r m m m m"
      "r m m m m"
      "r m m m m"
      "r m m m m"
      "r g g g g"
      "n f f f f ";
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}



.room-list {
  grid-area: r;
}
.new-room-form {
  grid-area: n;
}

.message-list {
  grid-area: m;
}

.send-message-form {
  grid-area: f;

}
.insert-user {
  grid-area: g;
}
